// 1. –ù–∞–ø–∏—Å–∞—Ç—å —Ñ—É–Ω–∫—Ü–∏—é map, –∫–æ—Ç–æ—Ä–∞—è –±—É–¥–µ—Ç –ø—Ä–∏–Ω–∏–º–∞—Ç—å –≤ –∫–∞—á–µ—Å—Ç–≤–µ
// –∞—Ä–≥—É–º–µ–Ω—Ç–∞ –º–∞—Å—Å–∏–≤ arr –∏ —Ñ—É–Ω–∫—Ü–∏—é, –∫–æ—Ç–æ—Ä–∞—è –≤ —Å–≤–æ—é –æ—á–µ—Ä–µ–¥—å –±—É–¥–µ—Ç
// –æ—Å—É—â–µ—Å—Ç–≤–ª—è—Ç—å –ø—Ä–µ–æ–±—Ä–∞–∑–æ–≤–∞–Ω–∏–µ –∫–∞–∂–¥–æ–≥–æ —ç–ª–µ–º–µ–Ω—Ç–∞ –º–∞—Å—Å–∏–≤–∞ –≤ –≤–∏–¥
// "<li class="list-group-item">Tom</li>"
// 2. –í—ã–≤–µ—Å—Ç–∏ —ç–ª–µ–º–µ–Ω—Ç—ã —Ç—Ä–∞–Ω—Å—Ñ–æ—Ä–º–∏—Ä–æ–≤–∞–Ω–Ω–æ–≥–æ –º–∞—Å—Å–∏–≤–∞ –≤ ul —Å id="list"
// –í—ã–≤–æ–¥ –¥–æ–ª–∂–µ–Ω –æ—Å—É—â–µ—Å—Ç–≤–ª—è—Ç—å—Å—è –≤ –æ—Ç—Å–æ—Ä—Ç–∏—Ä–æ–≤–∞–Ω–Ω–æ–º –ø–æ –∞–ª—Ñ–∞–≤–∏—Ç—É –≤–∏–¥–µ

let arr = ["Tom", "Steve", "Bill", "Rita", "Pete", "ashley"];
const list = document.getElementById("list");

list.innerHTML = map(arr, createList).sort().join("");

// –°–∏–≥–Ω–∞—Ç—É—Ä–∞ map
function map(arr, fn) {
  let listArr = [];

  for (let i = 0; i < arr.length; i++) listArr.push(fn(arr[i]));

  return listArr;
}

function createList(item) {
  return `<li class="list-group-item">${item}</li>`;
}
